import {
  AppBar,
  Toolbar,
  makeStyles,
  Button,
  IconButton,
  Drawer,
  Link,
  MenuItem,
  Container,
  Avatar
} from "@material-ui/core";
import { connect } from "react-redux";
import MenuIcon from "@material-ui/icons/Menu";
import React, { useState, useEffect, } from "react";
import { Link as RouterLink } from "react-router-dom";
import virtuslogo from "../../assets/images/logo/logo250X250.png";
import { setThemeMode } from "../../actions/news";
import "./index.css"

const headersData = [
  {
    label: "Create an offer",
    href: "/home",
  },
  {
    label: "Log In",
    href: "/news",
  },
  {
    label: "Register ",
    href: "/investing",
  },
];

const useStyles = makeStyles(() => ({
  conetent: {
    // backgroundColor: "#000000",
  },
  header: {
  },
  logo: {
    "display": "flex",
    "justifyContent": "space-between"
  },
  menuButton: {
    fontFamily: 'Inter',
    fontStyle: "normal",
    fontWeight: 800,
    fontSize: "17px",
    lineHeight: "21px",
    color: "#FFFFFF",
    textTransform: "capitalize",
    marginLeft: "7px",
    marginRight: "7px",
  },
  toolbar: {
    padding: 0,
    display: "flex",
    justifyContent: "space-between",
    height: "89px"
  },
  drawerContainer: {
    display: 'flow-root',
    maxWidth: '200px',
    padding: "20px 30px",
    background: "#000000",
    color: "#fff"
  },
  modeButton: {
    marginLeft: "25px",
    marginRight: "7px",
    minWidth: "0px"
  },
  menuloginButton: {
    fontFamily: 'Inter',
    fontStyle: "normal",
    fontWeight: 800,
    fontSize: "17px",
    lineHeight: "21px",
    color: "#FFFFFF",
    background: "rgba(188, 188, 188, 0.1)",
    borderRadius: "2px",
    marginLeft: "15px",
    marginRight: "7px",
    textTransform: "capitalize",
  },
  menusignupButton: {
    fontFamily: 'Inter',
    fontStyle: "normal",
    fontWeight: 800,
    fontSize: "17px",
    lineHeight: "21px",
    color: "#FFFFFF",
    background: "#154691",
    borderRadius: "2px",
    textTransform: "capitalize"
  },
  menuloginButtonMobile: {
    fontFamily: 'Inter',
    fontStyle: "normal",
    fontWeight: 800,
    fontSize: "17px",
    lineHeight: "21px",
    color: "#FFFFFF",
    background: "rgba(188, 188, 188, 0.1)",
    borderRadius: "2px",
    textTransform: "capitalize",
    width: "150px",
    marginBottom: "10px !important",
    margin: "0 auto"
  },
  menusignupButtonMobile: {
    fontFamily: 'Inter',
    fontStyle: "normal",
    fontWeight: 800,
    fontSize: "17px",
    lineHeight: "21px",
    color: "#FFFFFF",
    background: "#154691",
    borderRadius: "2px",
    textTransform: "capitalize",
    width: "150px",
    marginBottom: "10px !important",
    margin: "0 auto"
  },
  drawercss: {
    background: "#000000",
    color: "#fff"
  },
  desktopContent: {
    maxWidth: "1500px",
    padding: "1.5rem 1rem"
  },

}));

function Header(props) {
  const {
    header,
    logo,
    menuButton,
    toolbar,
    drawerContainer,
    conetent,
    desktopContent,


  } = useStyles();

  const [state, setState] = useState({
    mobileView: false,
    drawerOpen: false,
  });

  const { mobileView, drawerOpen } = state;

  useEffect(() => {
    const setResponsiveness = () => {
      return window.innerWidth < 900
        ? setState((prevState) => ({ ...prevState, mobileView: true }))
        : setState((prevState) => ({ ...prevState, mobileView: false }));
    };

    setResponsiveness();
    window.addEventListener("resize", () => setResponsiveness());

    var element = document.body;
    props.onChangeMode('dark');

    return () => {
      window.removeEventListener("resize", () => setResponsiveness());
    };
  }, []);


  const displayDesktop = () => {
    return (
      <Container maxWidth="lg" className={desktopContent} >
        <Toolbar className={toolbar}>
          {Logo}
          <div>
            {getMenuButtons()}
          </div>
        </Toolbar>
      </Container >
    );
  };

  const displayMobile = () => {
    const handleDrawerOpen = () =>
      setState((prevState) => ({ ...prevState, drawerOpen: true }));
    const handleDrawerClose = () =>
      setState((prevState) => ({ ...prevState, drawerOpen: false }));

    return (
      <Toolbar>
        <IconButton
          {...{
            edge: "start",
            color: "inherit",
            "aria-label": "menu",
            "aria-haspopup": "true",
            onClick: handleDrawerOpen,
            className: 'humber'
          }}
        >
          <MenuIcon />
        </IconButton>

        <Drawer
          {...{
            anchor: "left",
            open: drawerOpen,
            onClose: handleDrawerClose,
            className: 'drawer-mobile'
          }}
        >
          <div>{MobileLogo}</div>

          <div className={`${drawerContainer} drawerContainer`}>
            {getDrawerChoices()}

            {/* <Button
              onClick={(e) => {
                var element = document.body;
                element.classList.toggle("light-mode");
                props.onChangeMode('dark');
              }}
              {...{
                color: "inherit",
                className: `${modeButton} light-mode-button`,
                startIcon: < Avatar src={lightMode} />
              }}
            >

            </Button>

            <Button
              onClick={(e) => {
                var element = document.body;
                element.classList.toggle("light-mode");
                props.onChangeMode('light');
              }}
              {...{
                color: "inherit",
                className: `${modeButton} night-mode-button`,
                startIcon: < Avatar src={nightMode} />
              }}
            >
            </Button> */}

          </div>
        </Drawer>

        <div>{Logo}</div>

      </Toolbar>
    );
  };


  const getDrawerChoices = () => {
    return headersData.map(({ label, href }) => {
      return (
        <Link
          {...{
            component: RouterLink,
            to: href,
            color: "inherit",
            style: { textDecoration: "none" },
            key: label,
          }}
        >
          <MenuItem>{label}</MenuItem>
        </Link>
      );
    });
  };


  const Logo = (
    <div className={`${logo}`}>
      <RouterLink to={'/home'}>
        <img className={`mobile-logo`} src={virtuslogo} width="100" />
      </RouterLink>
      {/* <span className="logotext">Virtus Junxit</span> */}
      <span className="logotext">NOM</span>
    </div>
  );

  const MobileLogo = (
    <div>
      <RouterLink to={'/home'}>
        <img className={`mobile-logo`} src={virtuslogo} width="100"  />
      </RouterLink>
      
    </div>
  );

  const getMenuButtons = () => {
    return headersData.map(({ label, href }) => {
      return (
        <Button
          onClick={(e) => {
            localStorage.removeItem('selectedNewsPage');
            localStorage.removeItem('selectedInvestPage');
            localStorage.removeItem('selectedStockPage');
          }}
          {...{
            key: label,
            color: "inherit",
            to: href,
            component: RouterLink,
            className: `${menuButton} nav-bar`,
          }}
        >
          {label}
        </Button>
      );
    });
  };

  return (
    <AppBar className={header} elevation={0} position="relative">
      {mobileView ? displayMobile() : displayDesktop()}
    </AppBar>
  );
}

const mapDispatchToProps = (dispatch) => {
  return {
    setThemeMode: (data) => dispatch(setThemeMode(data)),
  };
}


export default connect(null, mapDispatchToProps)(Header);