// @flow
import * as TYPES from '../actions/types';

const INIT_STATE = {
    themeMode: 'ligth',
};

const Reducer = (state = INIT_STATE, action) => {
    switch (action.type) {
        case TYPES.SET_THEME_MODE:
            return {
                ...state,
                themeMode: action.themeMode
            };
        default:
            return state;
    }
};

export default Reducer;
